.paging-container{
    border-bottom: 2px solid @lightest_gray;
    margin-left: -25px;
    margin-right: -25px;
}

.sidebar-button__structure {
  font-size: 50px;
  color: @dark_cyan;
  text-decoration: none !important;
  cursor: pointer;
}

.sidebar-button-container{
  display: inline-block;
  width: 80px;
  text-align: center;
  pointer-events: auto;
}

.sidebar-button__layout {
  pointer-events: none;
  position: fixed;
  top: 60px;
  z-index: 1040;
  left: 0px;
  right: 0px;
  text-align: right;
  -moz-transition: all 0.25s ease-out;
  -o-transition: all 0.25s ease-out;
  transition: all 0.25s ease-out;
  &.transfrom-slim {
    top: 25px !important;
    font-size: 35px !important;  
  }
  > i {
    vertical-align: middle;
  }
  &.menu-opened{
    // top: @buttonposition !important;
  }
}


.sidebar__layout {
  position: absolute;
  right: 27px;
  z-index: 1029;
  &:before{
    display: block;
    content: "";
    width: 0px;
    height: 0px;
    border-bottom: 10px solid #005958;
    border-right: 10px solid transparent;
    border-left: 10px solid transparent;
    position: absolute;
    right: 6px;
    top: -10px;
    z-index: 1;
  }
}


